#nav {
  font-family: Titillium Web, sans-serif;
  background-color: #a8395c; }
  #nav nav {
    padding-left: 0; }
    #nav nav h1 {
      font-size: 1em;
      line-height: 44px;
      font-weight: 600;
      margin: 0;
      color: #fff; }
    #nav nav img {
      max-height: 44px;
      float: left; }
    #nav nav li a:hover {
      -webkit-transition: all 0.5s ease;
      -moz-transition: all 0.5s ease;
      -ms-transition: all 0.5s ease;
      -o-transition: all 0.5s ease;
      transition: all 0.5s ease; }
    #nav nav li .dropdown-menu {
      position: absolute; }
      #nav nav li .dropdown-menu .dropdown-item {
        min-width: 250px;
        font-weight: 600;
        color: #797979;
        width: auto;
        margin: 0 5px;
        padding: 10px;
        -webkit-transition: all 0.5s ease;
        -moz-transition: all 0.5s ease;
        -ms-transition: all 0.5s ease;
        -o-transition: all 0.5s ease;
        transition: all 0.5s ease; }
        #nav nav li .dropdown-menu .dropdown-item:hover {
          background: rgba(0, 0, 0, 0.1); }
      #nav nav li .dropdown-menu i {
        color: #a8395c;
        font-size: 1.2em; }
    #nav nav li .arrow {
      border-style: solid;
      border-width: 0 8px 8px;
      border-color: transparent transparent white;
      margin-top: -50px;
      margin: -15px 15px;
      float: right; }
    #nav nav li a:active {
      background: rgba(0, 0, 0, 0.1); }
    #nav nav .search {
      background-color: transparent;
      color: #fff;
      border: 1px solid #a8395c;
      z-index: 2; }
      #nav nav .search:focus {
        background: rgba(0, 0, 0, 0.1);
        border-color: #fff;
        outline: 0;
        box-shadow: none; }
      #nav nav .search i:before {
        margin-left: -40px;
        z-index: 1; }

.box-tooltip {
  text-transform: uppercase;
  background: #fff;
  color: #7fcadd;
  cursor: pointer;
  display: inline-block;
  position: relative;
  margin: 0 5px;
  padding: 5px;
  font-size: 1.4em; }
  .box-tooltip:hover .tooltip {
    opacity: 1;
    pointer-events: auto; }
  .box-tooltip .tooltip {
    background: #fff;
    bottom: 100%;
    color: #4c4c4c;
    border: 2px solid #c9c9c9;
    display: block;
    left: -25px;
    margin-bottom: 15px;
    opacity: 0;
    padding: 10px;
    position: absolute;
    width: max-content;
    border-radius: 8px;
    box-shadow: 0 2px 2px #ccc;
    -webkit-transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
    -ms-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    transition: all 0.5s ease; }
    .box-tooltip .tooltip:after {
      border-left: solid transparent 10px;
      border-right: solid transparent 10px;
      border-top: solid #c9c9c9 10px;
      bottom: -10px;
      content: " ";
      height: 0;
      left: 50%;
      margin-left: -13px;
      position: absolute;
      width: 0; }

.wrapper {
  border: 1px solid #c9c9c9;
  padding: 15px;
  box-shadow: 0 2px 2px #ccc;
  margin: 20px 0; }

#video .video {
  position: relative;
  padding-bottom: 51.25%;
  /* 16:9 */
  padding-top: 25px;
  height: 0; }
  #video .video iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }

#video .wrapper {
  margin: 20px 0; }
  #video .wrapper .box-tooltip {
    float: right; }

#resultsLft #results, #resultsVids #results {
