  margin: 0;
  padding: 0;
  max-height: 550px;
  overflow: auto;
  position: relative; }
  #resultsLft #results .item, #resultsVids #results .item {
    padding-bottom: 20px;
    position: relative; }
    #resultsLft #results .item .title, #resultsVids #results .item .title {
      line-height: 1.1em;
      padding: 0;
      margin: 0; }
    #resultsLft #results .item img, #resultsVids #results .item img {
      max-width: 175px;
